.ModalTask{
    width: 650px;
    height: 700px;
    position: fixed;
    top: calc(50% - 350px);
    left: calc(50% - 325px);
    background-color: white;
    border: 5px solid rgba(117, 32, 255, 0.504);
    padding: 25px;
    font-size: 18px;
    z-index: 2;
    overflow: auto;
    border-radius: 10px;
    .Resume_Inpit{
        height: auto;
        width: 100%;
        resize: none;
    }
    .Modal_Activity{
        b{
            display: block;
            margin-bottom: 8px;
        }
    }
    .Modal__Activity_Count{
        height: inherit;    
    }
    .Modal__Activity_Scroll{
        min-height: auto;
        max-height: 150px;
        overflow: auto; 
    }
    .Modal__Activity_Scroll::-webkit-scrollbar {
        width: 15px;
      }
      
    .Modal__Activity_Scroll::-webkit-scrollbar-track {
        background: #f5c9f9;
        border-radius: 20px;
      }
      
    .Modal__Activity_Scroll::-webkit-scrollbar-thumb {
        background-color: rgba(117, 32, 255, 0.7);
        border-radius: 20px;
        border: 3px solid rgba(117, 32, 255, 0.7);
      }  
    .ModalTask_Wrapper{
        padding: 5px;
        .Left_TaskChilCont{
            overflow: auto;
            min-height: auto;
            max-height: 180px;
            width: 100%;
            margin-bottom: 15px;
        }
        .Left_TaskChilCont::-webkit-scrollbar {
            width: 15px;
          }
          
        .Left_TaskChilCont::-webkit-scrollbar-track {
            background: #f5c9f9;
            border-radius: 20px;
          }
          
        .Left_TaskChilCont::-webkit-scrollbar-thumb {
            background-color: rgba(117, 32, 255, 0.7);
            border-radius: 20px;
            border: 3px solid rgba(117, 32, 255, 0.7);
          }   
        .Left_TaskChilCont_item{
            width: 100%;
            padding: 10px;
        }
       
        .Wrapper_Form{
            margin-bottom: 40px;
            display: grid;
            row-gap: 8px;
            width: 100%;
            button{
                font-size: 18px;
            }
            button:hover{
                color: rgb(117, 32, 255)
            }
            .Form_BtnAddChild{
                width: 150px;
                font-size: 18px;
                border: 3px solid rgba(117, 32, 255, 1);
                padding: 3px;
                border-radius: 5px;
                color: rgba(117, 32, 255, 1);
                font-weight: bold;
            }
            .Form_BtnAddChild:hover{
                background-color: rgb(223, 206, 253);
            }
            .ModalTask_Resume{
                width: 100%;
                min-height: 20px;
                word-break:break-all;
                border: 3px solid rgb(187, 184, 184);
                border-radius: 8px;
                padding: 5px;
            }

            .ModalTask_Resume:hover, h3:hover{
                background-color: rgb(216, 212, 212);
                border-radius: 8px;

            }
            select{
                font-size: 16px;
                cursor: pointer;
                width: 200px;
            }
        }
       
        h3{
            padding: 5px; 
        }
       
        input{
            padding: 4px;
        cursor: pointer;
        font-size: 18px;
        border-radius: 8px;
        }
        select{
            border-radius: 8px;
            width: 100px;
            border: 3px solid rgba(117, 32, 255, 0.762);
            font-weight: bold;
            outline: none;
        }
    }
    
    .ModalTask_Close{
        float: right;
        font-weight: bold;
    }
    .ModalTask_Close:hover{
        color: rgb(117, 32, 255)
    }
}

.ModalTask::-webkit-scrollbar {
    width: 15px;
  }
  
.ModalTask::-webkit-scrollbar-track {
    background: #f5c9f9;
    border-radius: 20px;
  }
  
  .ModalTask::-webkit-scrollbar-thumb {
    background-color: rgba(117, 32, 255, 0.7);
    border-radius: 20px;
    border: 3px solid rgba(117, 32, 255, 0.7);
  }